export const isbn13 = (str: string | Array<any> | any) => {
    let i13Bool = false
    let isValid = false;
    if (str.length === 13) {
        str = str.split("");
        str = str.map((el: any) => parseInt(el)).map((el: any, index: any) => el * (index % 2 == 0 ? 1 : 3));
        isValid = str.reduce((a: any, b: any) => a + b, 0) % 10 === 0;
        return isValid ? "Valid" : "Invalid";

    } else if (str.length === 10) {
        let strContainer = [];
        let strCounter = 11
        let weightBool = false
        // str = "978" + 
        str.slice(0, 9);
        str = str.split("");

        for (let index = 0; index < str.length; index++) {
            weightBool = !weightBool;
            strContainer[index] = parseInt(str[index]) * strCounter--;
        }

        let sum = strContainer.reduce((a, b) => a + b);
        console.log(sum);

        let checkDigit = (10 - (sum % 10)) % 10;
        str = str.join("").replace(/,/g, "");

        return str + checkDigit.toString();
        
    }

    

};


console.log(isbn13("0316066524"))

